// –î–æ–¥–∞—î–º–æ –ø–æ–¥—ñ—é DOMContentLoaded
document.addEventListener('DOMContentLoaded', () => {
  const goToTopButton = document.getElementById('goToTop');

  // –ü–æ–∫–∞–∑—É–≤–∞—Ç–∏ –∫–Ω–æ–ø–∫—É –ø—ñ—Å–ª—è –ø–µ–≤–Ω–æ—ó –∫—ñ–ª—å–∫–æ—Å—Ç—ñ –ø—ñ–∫—Å–µ–ª—ñ–≤ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏
  window.addEventListener('scroll', () => {
    if (window.scrollY > 300) {
      // –ó–º—ñ–Ω—é–π—Ç–µ –∑–Ω–∞—á–µ–Ω–Ω—è "300" –∑–∞ –ø–æ—Ç—Ä–µ–±–∏
      goToTopButton.style.display = 'flex';
    } else {
      goToTopButton.style.display = 'none';
    }
  });

  // –ü–æ–≤–µ—Ä–Ω–µ–Ω–Ω—è –¥–æ –≤–µ—Ä—Ö—É –ø—Ä–∏ –∫–ª—ñ–∫—É –Ω–∞ –∫–Ω–æ–ø–∫—É
  goToTopButton.addEventListener('click', () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth', // –ü–ª–∞–≤–Ω–∞ –ø—Ä–æ–∫—Ä—É—Ç–∫–∞
    });
  });
});
