const backdrop = document.querySelector('.backdrop');
const openMenuBtn = document.querySelector('.burger-menu-wrap');
const closeMenuBtn = document.querySelector('.menu-close-btn');
const overlay = document.querySelector('.backdrop-overlay');
const menuItems = document.querySelectorAll('.mob-menu-item a');

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è –º–µ–Ω—é
const openMenu = () => {
  backdrop.classList.add('is-open');
  overlay.classList.add('is-open');
  // –†–æ–±–∏–º–æ –µ–ª–µ–º–µ–Ω—Ç–∏ –≤–∏–¥–∏–º–∏–º–∏
  backdrop.style.visibility = 'visible';
  overlay.style.visibility = 'visible';
};

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–∞–∫—Ä–∏—Ç—Ç—è –º–µ–Ω—é
const closeMenu = () => {
  // –í–∏–¥–∞–ª—è—î–º–æ –∫–ª–∞—Å–∏ –¥–ª—è –ø–ª–∞–≤–Ω–æ–≥–æ –∑–∞–∫—Ä–∏—Ç—Ç—è
  backdrop.classList.remove('is-open');
  overlay.classList.remove('is-open');
  // –ß–µ—Ä–µ–∑ 300 –º—Å –∑–º—ñ–Ω—é—î–º–æ visibility
  setTimeout(() => {
    backdrop.style.visibility = 'hidden';
    overlay.style.visibility = 'hidden';
  }, 300); // –í—ñ–¥–ø–æ–≤—ñ–¥–∞—î —Ç—Ä–∏–≤–∞–ª–æ—Å—Ç—ñ transition
};

// –ó–∞–∫—Ä–∏–≤–∞—î–º–æ –º–µ–Ω—é –ø—Ä–∏ –∫–ª—ñ–∫—É –Ω–∞ –±—É–¥—å-—è–∫–∏–π –ø—É–Ω–∫—Ç –º–µ–Ω—é
menuItems.forEach(item => {
  item.addEventListener('click', () => {
    closeMenu();
  });
});

// –ó–∞–∫—Ä–∏–≤–∞—î–º–æ –º–µ–Ω—é –ø—Ä–∏ –∫–ª—ñ–∫—É –Ω–∞ –æ–≤–µ—Ä–ª–µ–π
overlay.addEventListener('click', () => {
  closeMenu();
});

// –î–æ–¥–∞—î–º–æ –æ–±—Ä–æ–±–Ω–∏–∫–∏ –ø–æ–¥—ñ–π –¥–ª—è –∫–Ω–æ–ø–æ–∫ –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è —Ç–∞ –∑–∞–∫—Ä–∏—Ç—Ç—è
openMenuBtn.addEventListener('click', openMenu);
closeMenuBtn.addEventListener('click', closeMenu);
