document.addEventListener('DOMContentLoaded', () => {
  const toolsItems = document.querySelectorAll('.tools-list-item');

  toolsItems.forEach(item => {
    item.addEventListener('click', () => {
      // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ —á–∏ –µ–ª–µ–º–µ–Ω—Ç –≤–∂–µ –ø–µ—Ä–µ–≤–µ—Ä–Ω—É—Ç–∏–π
      const isFlipped = item.classList.contains('flip');

      // –°–∫–∏–¥–∞—î–º–æ –ø–µ—Ä–µ–≤–µ—Ä—Ç–∞–Ω–Ω—è –¥–ª—è –≤—Å—ñ—Ö –∫–∞—Ä—Ç–æ–∫
      toolsItems.forEach(el => el.classList.remove('flip'));

      // –ü–µ—Ä–µ–≤–µ—Ä—Ç–∞—î–º–æ —Ç—ñ–ª—å–∫–∏ –∫–ª—ñ–∫–Ω—É—Ç—É –∫–∞—Ä—Ç–∫—É, —è–∫—â–æ –≤–æ–Ω–∞ —â–µ –Ω–µ –±—É–ª–∞ –ø–µ—Ä–µ–≤–µ—Ä–Ω—É—Ç–∞
      if (!isFlipped) {
        item.classList.add('flip');
      }
    });
  });
});
