function updateHeroHeight() {
  const heroSection = document.querySelector('.container-header-hero');
  heroSection.style.minHeight = `${window.innerHeight}px`;
}

// –í—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω—è –≤–∏—Å–æ—Ç–∏ –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ —Å—Ç–æ—Ä—ñ–Ω–∫–∏
updateHeroHeight();

// –û–Ω–æ–≤–ª–µ–Ω–Ω—è –≤–∏—Å–æ—Ç–∏ –ø—Ä–∏ –∑–º—ñ–Ω—ñ —Ä–æ–∑–º—ñ—Ä—É –≤—ñ–∫–Ω–∞
window.addEventListener('resize', updateHeroHeight);
