window.calculatePlan = function calculatePlan() {
  const plans = [
    { name: 'STERYL 20', packets: 20, price: 110.0 },
    { name: 'STERYL 30', packets: 30, price: 140.0 },
    { name: 'STERYL 50', packets: 50, price: 210.0 },
    { name: 'STERYL 100', packets: 100, price: 300.0 },
    { name: 'STERYL 150', packets: 150, price: 360.0 },
    { name: 'STERYL 200', packets: 200, price: 430.0 },
    { name: 'STERYL 300', packets: 300, price: 550.0 },
    { name: 'STERYL 500', packets: 500, price: 780.0 },
  ];

  // –û—Ç—Ä–∏–º—É—î–º–æ –∑–Ω–∞—á–µ–Ω–Ω—è –∫—ñ–ª—å–∫–æ—Å—Ç—ñ –ø–∞–∫–µ—Ç—ñ–≤
  const packets = parseInt(document.querySelector('#packets').value);
  const packetsInput = document.querySelector('#packets');
  const resultDiv = document.querySelector('#result');
  const calcButton = document.querySelector('.calc-button');

  // –í–∞–ª—ñ–¥–∞—Ü—ñ—è –≤–≤–µ–¥–µ–Ω–∏—Ö –¥–∞–Ω–∏—Ö
  if (isNaN(packets) || packets <= 0) {
    resultDiv.innerHTML = 'Proszƒô wpisaƒá poprawnƒÖ liczbƒô pakiet√≥w.';
    return;
  }
  if (packets > 1000) {
    resultDiv.innerHTML = `
      Masz du≈ºy wolumen narzƒôdzi do sterylizacji. <br>
      Warto skontaktowaƒá siƒô z nami, aby ustaliƒá indywidualne warunki wsp√≥≈Çpracy.
    `;
    packetsInput.value = ''; // –û—á–∏—â–∞—î–º–æ –ø–æ–ª–µ –≤–≤–æ–¥—É
    calcButton.blur(); // –ó–Ω—ñ–º–∞—î–º–æ —Ñ–æ–∫—É—Å –∑ –∫–Ω–æ–ø–∫–∏
    return;
  }

  let bestPlan = null;
  let lowestTotalCost = Infinity;

  plans.forEach(plan => {
    const costPerPacket = plan.price / plan.packets; // –í–∞—Ä—Ç—ñ—Å—Ç—å –æ–¥–Ω–æ–≥–æ –ø–∞–∫–µ—Ç–∞
    let totalCost = plan.price;

    // –î–æ–¥–∞–≤–∞–Ω–Ω—è –≤–∞—Ä—Ç–æ—Å—Ç—ñ –¥–æ–¥–∞—Ç–∫–æ–≤–∏—Ö –ø–∞–∫–µ—Ç—ñ–≤
    if (packets > plan.packets) {
      const extraPackets = packets - plan.packets;
      totalCost += extraPackets * costPerPacket;
    }

    // –ü–æ—à—É–∫ –Ω–∞–π–∫—Ä–∞—â–æ–≥–æ –ø–ª–∞–Ω—É
    if (totalCost < lowestTotalCost) {
      lowestTotalCost = totalCost;
      bestPlan = {
        ...plan,
        totalCost: totalCost,
        extraPackets: packets > plan.packets ? packets - plan.packets : 0,
        costPerPacket: costPerPacket.toFixed(2),
      };
    }
  });

  // –í—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —Ä–µ–∑—É–ª—å—Ç–∞—Ç—É
  if (bestPlan) {
    resultDiv.innerHTML = `
      Przy ${packets} pakietach miesiƒôcznie najlepszym wyborem dla Ciebie jest abonament 
      <strong>${bestPlan.name}</strong>.<br>
      Koszt sterylizacji jednego pakietu w tym abonamencie wynosi <strong>${
        bestPlan.costPerPacket
      } z≈Ç</strong>.<br>
      ${
        bestPlan.extraPackets > 0
          ? `Miesiƒôczny koszt tego abonamentu to <strong>${bestPlan.price.toFixed(
              2
            )} z≈Ç </strong>, <br>
			   koszt sterylizacji ${
           bestPlan.extraPackets
         } pakiet√≥w poza abonamentem wynosi <strong>${(
              bestPlan.extraPackets * bestPlan.costPerPacket
            ).toFixed(2)} z≈Ç.</strong>`
          : 'Brak dodatkowych koszt√≥w. <br>'
      }
					<br> Ca≈Çkowity koszt sterylizacli ${packets} pakiet√≥w miesiecznie z abonamentem <strong>${
      bestPlan.name
    }</strong> wynosi <strong>${bestPlan.totalCost.toFixed(2)} z≈Ç. </strong>
					`;
  } else {
    resultDiv.innerHTML = 'Nie znaleziono odpowiedniego abonamentu.';
  }
  packetsInput.value = '';
  calcButton.blur();
};
